<template>
<div class="vue" v-show="info.name">

    <div class="teacherTabbar">
        <div :class="type === 'reservel' ? 'order active' : 'order'" @click="change('reservel')">预约</div>
        <div :class="type === 'details' ? 'detail active' : 'detail'" @click="change('details')">详情</div>
    </div>

    <div class="" v-show="type === 'reservel'">
        <div class="teacherImg">
            <img :src="info.teacherImg" alt="">
        </div>


        <div class="main">
            <div class="title">
                <div class="cont">{{info.name}}</div>
                <div class="line"></div>
            </div>

            <div v-for="item in info.school">
                <div class="school">{{item.schoolName}}</div>
                <div class="address">{{item.address}}</div>
                <div class="classDateBox">
                    <div class="classDate" v-for="i in item.dateTime" @click="checkUserState">{{i}}</div>
                </div>
            </div>
        </div>
    </div>

    <div class="" v-show="type === 'details'">
        <img :src="info.detailImg" alt="">
    </div>

</div>
</template>

<script>
import {Indicator} from 'mint-ui';
import check from '../libs/utils'

export default {
    data() {
        return {
            info: {},
            type: 'reservel',
            findCache: false
        }
    },
    mounted() {
        let session_teacher = window.window.sessionStorage.teacher;
        let id = this.$route.params.id; //query string
        let that = this;
        if (session_teacher) {
            let oTeacher = JSON.parse(session_teacher);
            oTeacher.forEach((item, index) => {
                if (item.id === id) {
                    that.info = item;
                    that.findCache = true;
                }
            });

        }
        if (!this.findCache) {
            Indicator.open({
                spinnerType: 'triple-bounce'
            });
            this.getInfo(id);
        }
    },
    methods: {
        getInfo(id) {
            let that = this;

            // $.ajax({
            //     type: 'POST',
            //     url: 'https://cnodejs.org/api/v1/accesstoken',
            //     data: {
            //         accesstoken: this.token
            //     },
            //     dataType: 'json',
            //     success: (res) => {
            //         let user = {
            //             loginname: res.loginname,
            //             avatar_url: res.avatar_url,
            //             userId: res.id,
            //             token: this.token
            //         };
            //         window.window.sessionStorage.user = JSON.stringify(user);
            //         this.$store.dispatch('setUserInfo', user);
            //         let redirect = decodeURIComponent(this.$route.query.redirect || '/');
            //         this.$router.push({
            //             path: redirect
            //         });
            //     },
            //     error: (res) => {
            //         var error = JSON.parse(res.responseText);
            //         this.$alert(error.error_msg);
            //     }
            // });
            //mock ajax
            setTimeout(() => {
                let info = {
                    id: id,
                    teacherImg: 'http://img2.imgtn.bdimg.com/it/u=2723553901,3363603899&fm=11&gp=0.jpg',
                    name: '刘丽老师',
                    school: [{
                        schoolName: '赞清河学堂',
                        address: '风光路商贸大厦4号楼2单元1101',
                        dateTime: [
                            '6.12-6.16',
                            '6.12-6.16',
                            '6.12-6.16',
                        ]
                    }, {
                        schoolName: '小北门学堂',
                        address: '风光路商贸大厦4号楼2单元1101',
                        dateTime: [
                            '6.12-6.16',
                            '6.12-6.16',
                            '6.12-6.16',
                        ]
                    }, ],
                    detailImg: 'http://image2.xyzs.com/upload/6b/18/1013/20141204/141769614615369_0.jpg'
                };
                Indicator.close();
                that.info = info;
                let cacheStorage = window.window.sessionStorage.teacher;
                if (cacheStorage) {
                    console.log();
                    arr.push(info);
                    window.window.sessionStorage.teacher = JSON.stringify(arr); //
                } else {
                    window.window.sessionStorage.teacher = "[" + JSON.stringify(info) + "]";
                }

            }, 2000);
        },
        change(type) {
            this.type = type;
        },
        checkUserState () {
            let boolen = check.checkToken(this);
            console.log(boolen);
            boolen === true ? this.goReservel() : this.goLogin();
        },
        goReservel() {
            this.$router.push({
                name: 'signUp'
            });
        },
        goLogin() {
            this.$router.push({
                name: 'login'
            });
        }
    }
}
</script>

<style lang="scss" scoped>
img {
    display: block;
    width: 100%;
}

.teacherTabbar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background: #fff;
    position: relative;
    div {
        font-size: 0.16rem;
        color: #666;
        text-align: center;
        line-height: 0.44rem;
        -webkit-box-flex: 1;
        -ms-flex: 1;
        flex: 1;
    }
    .active {
        color: #bd8747;
    }
    .active::after {
        content: "";
        width: 0.44rem;
        height: 0.01rem;
        position: absolute;
        bottom: 0;
        left: 0.73rem;
        background: #bd8747;
    }
}
/*line and point, name in center*/
.title {
    margin-top: 0.12rem;
    line-height: 0.3rem;
    height: 0.3rem;
    font-size: 0.16rem;
    text-align: center;
    position: relative;
    .line {
        width: 3.55rem;
        height: 0.01rem;
        background: #ddd;
        position: absolute;
        top: 49%;
        left: 0.1rem;
        /* z-index: 0;*/
    }
    .cont {
        width: 0.96rem;
        line-height: 0.3rem;
        height: 0.3rem;
        position: absolute;
        top: 0;
        left: 1.4rem;
        background: #eee;
        z-index: 1;
    }
    .cont::before {
        content: "";
        width: 0.04rem;
        height: 0.04rem;
        border-radius: 0.04rem;
        position: absolute;
        left: 0.06rem;
        top: 47%;
        background: #ddd;
    }
    .cont::after {
        content: "";
        width: 0.04rem;
        height: 0.04rem;
        border-radius: 0.04rem;
        position: absolute;
        right: 0.06rem;
        top: 45%;
        background: #ddd;
    }
}
.school {
    line-height: 0.31rem;
    color: #bd8747;
    font-size: 0.16rem;
    text-align: center;
}
.address {
    line-height: 0.28rem;
    color: #999;
    font-size: 0.13rem;
    text-align: center;
    margin-bottom: 0.12rem;
}
//class
.classDate {
    height: 0.44rem;
    line-height: 0.44rem;
    text-indent: 0.15rem;
    font-size: 0.16rem;
    background: #fff url('../assets/images/right.png') no-repeat 3.52rem center;
    background-size: 0.16rem;
    border-bottom: 0.01rem solid #ddd;
}
.classDateBox div:nth-child(3n) {
    border: 0;
    margin-bottom: 0.11rem;
}
.teacherImg {
    height: 2rem;
    img {
        height: 100%;
    }
}
</style>



